@use 'colors';

@include colors.color-setter(background-image);

.bg {
    animation: slide 3s ease-in-out infinite alternate;
    bottom: 0;
    left: -50%;
    opacity: .5;
    position: fixed;
    right: -50%;
    top: 0;
    z-index: -1;
}

.bg2 {
    @extend .bg;
    animation-direction: alternate-reverse;
    animation-duration: 4s;
}
  
.bg3 {
    @extend .bg;
    animation-duration: 5s;
}

@keyframes slide {
    0% {
        transform: translateX(-25%);
    }
    100% {
        transform: translateX(25%);
    }
}